
.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 70px;
    padding: 60px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: #a8a8aa5d;
    z-index: 1;
  }
  
  .product > img {
    max-height: 300px;
    width: 100%;
    object-fit: contain;
  }
